'use client';

import { useState } from 'react';

interface ShareProductProps {
  productId: string;
  productName: string;
  productImage?: string;
  className?: string;
  size?: 'sm' | 'md' | 'lg';
}

export default function ShareProduct({ 
  productId, 
  productName, 
  productImage,
  className = '',
  size = 'md' 
}: ShareProductProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [copied, setCopied] = useState(false);

  const sizes = {
    sm: 'w-4 h-4',
    md: 'w-5 h-5',
    lg: 'w-6 h-6'
  };

  const buttonSizes = {
    sm: 'px-2 py-1 text-xs',
    md: 'px-3 py-2 text-sm',
    lg: 'px-4 py-3 text-base'
  };

  const productUrl = `${typeof window !== 'undefined' ? window.location.origin : ''}/products/${productId}`;
  const shareText = `Check out ${productName} from Raaga Foods - authentic traditional flavors!`;

  const shareOptions = [
    {
      name: 'WhatsApp',
      icon: '📱',
      color: 'bg-green-600 hover:bg-green-700',
      url: `https://wa.me/?text=${encodeURIComponent(`${shareText}\n\n${productUrl}`)}`
    },
    {
      name: 'Facebook',
      icon: '📘',
      color: 'bg-blue-600 hover:bg-blue-700',
      url: `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(productUrl)}&quote=${encodeURIComponent(shareText)}`
    },
    {
      name: 'Twitter',
      icon: '🐦',
      color: 'bg-sky-500 hover:bg-sky-600',
      url: `https://twitter.com/intent/tweet?text=${encodeURIComponent(shareText)}&url=${encodeURIComponent(productUrl)}`
    },
    {
      name: 'Instagram',
      icon: '📷',
      color: 'bg-pink-600 hover:bg-pink-700',
      url: 'https://www.instagram.com/', // Instagram doesn't support direct sharing via URL
      action: 'copy' // We'll copy the link for Instagram
    },
    {
      name: 'LinkedIn',
      icon: '💼',
      color: 'bg-blue-700 hover:bg-blue-800',
      url: `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(productUrl)}`
    },
    {
      name: 'Email',
      icon: '📧',
      color: 'bg-gray-600 hover:bg-gray-700',
      url: `mailto:?subject=${encodeURIComponent(`Check out ${productName} from Raaga Foods`)}&body=${encodeURIComponent(`${shareText}\n\n${productUrl}`)}`
    }
  ];

  const handleShare = (url: string, platform: string, action?: string) => {
    if (action === 'copy' || platform === 'Instagram') {
      handleCopyLink();
      alert('Link copied! You can now paste it on Instagram.');
    } else {
      // Track share event (you can add analytics here)
      console.log(`Shared ${productName} on ${platform}`);
      
      // Open share URL
      window.open(url, '_blank', 'noopener,noreferrer,width=600,height=400');
    }
    setIsOpen(false);
  };

  const handleCopyLink = async () => {
    try {
      await navigator.clipboard.writeText(productUrl);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (error) {
      console.error('Failed to copy link:', error);
      // Fallback for older browsers
      const textArea = document.createElement('textarea');
      textArea.value = productUrl;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  const handleNativeShare = async () => {
    if (navigator.share) {
      try {
        await navigator.share({
          title: productName,
          text: shareText,
          url: productUrl,
        });
      } catch (error) {
        console.log('Share cancelled or failed');
      }
    }
  };

  return (
    <div className={`relative ${className}`}>
      {/* Share Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className={`inline-flex items-center space-x-2 ${buttonSizes[size]} bg-orange-100 text-orange-600 hover:bg-orange-200 rounded-lg transition-colors duration-200 border border-orange-200 hover:border-orange-300`}
        title="Share this product"
      >
        <svg className={sizes[size]} fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
        </svg>
        
      </button>

      {/* Share Dropdown */}
      {isOpen && (
        <>
          {/* Backdrop */}
          <div 
            className="fixed inset-0 z-40" 
            onClick={() => setIsOpen(false)}
          />
          
          {/* Share Options */}
          <div className="absolute top-full mt-2 left-0 z-50 bg-white rounded-lg shadow-xl border border-gray-200 min-w-80 p-4">
            <div className="flex justify-between items-center mb-4">
              <h3 className="font-semibold text-gray-900">Share this product</h3>
              <button
                onClick={() => setIsOpen(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            {/* Product Info */}
            <div className="flex items-center gap-3 mb-4 p-2 bg-gray-50 rounded-lg">
              {productImage && (
                <img 
                  src={productImage} 
                  alt={productName}
                  className="w-12 h-12 object-cover rounded"
                />
              )}
              <div className="flex-1">
                <p className="font-medium text-gray-900 text-sm">{productName}</p>
                <p className="text-xs text-gray-500">Raaga Foods</p>
              </div>
            </div>

            {/* Share Options Grid */}
            <div className="grid grid-cols-3 gap-3 mb-4">
              {shareOptions.map((option) => (
                <button
                  key={option.name}
                  onClick={() => handleShare(option.url, option.name, option.action)}
                  className={`flex flex-col items-center p-3 rounded-lg text-white transition-all duration-200 hover:scale-105 ${option.color}`}
                  title={`Share on ${option.name}`}
                >
                  <span className="text-2xl mb-1">{option.icon}</span>
                  <span className="text-xs font-medium">{option.name}</span>
                </button>
              ))}
            </div>

            {/* Native Share & Copy Link */}
            <div className="space-y-2">
              {navigator.share && (
                <button
                  onClick={handleNativeShare}
                  className="w-full flex items-center justify-center gap-2 p-3 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
                >
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
                  </svg>
                  <span className="text-sm font-medium">Share with device apps</span>
                </button>
              )}
              
              <button
                onClick={handleCopyLink}
                className={`w-full flex items-center justify-center gap-2 p-3 border rounded-lg transition-all duration-200 ${
                  copied 
                    ? 'border-green-300 bg-green-50 text-green-700' 
                    : 'border-gray-300 hover:bg-gray-50'
                }`}
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                </svg>
                <span className="text-sm font-medium">
                  {copied ? 'Link copied!' : 'Copy link'}
                </span>
              </button>
            </div>

            {/* URL Display */}
            <div className="mt-3 p-2 bg-gray-100 rounded text-xs text-gray-600 break-all">
              {productUrl}
            </div>
          </div>
        </>
      )}
    </div>
  );
}
